import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "animate.css/animate.min.css";
import "modern-normalize/modern-normalize.css";

import HtmlHead from "./components/HtmlHead";
import Menu from "./components/Menu";
import Home from "./components/Home";

import About from "./components/pages/About";
import Team from "./components/pages/Team";
import WhyStellar from "./components/pages/WhyStellar";
import Contact from "./components/pages/Contact";
import CommunityGuidelines from "./components/pages/CommunityGuidelines";
import Privacy from "./components/pages/Privacy";
import Terms from "./components/pages/Terms";
import Gdpr from "./components/pages/Gdpr";
import Faq from "./components/pages/Faq";
import Creator from "./components/Creator";
import Creators from "./components/Creators";
import Workflow from "./components/Workflow";
import Security from "./components/pages/Security";

import FooterLinks from "./components/FooterLinks";
import FooterCredits from "./components/FooterCredits";

class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <HtmlHead />
          <Menu />
          <Route exact path="/" component={Home} />
          <div className="ui container">
            <Route exact path="/about/akolade" component={About} />
            <Route exact path="/about/team" component={Team} />
            <Route exact path="/creators/all" component={Creators} />

            <Route
              exact
              path="/about/why-stellar-lumens"
              component={WhyStellar}
            />
            <Route exact path="/about/contact" component={Contact} />
            <Route exact path="/about/privacy-policy" component={Privacy} />
            <Route exact path="/about/terms-of-service" component={Terms} />
            <Route exact path="/about/gdpr-compliance" component={Gdpr} />
            <Route exact path="/about/faq" component={Faq} />
            <Route exact path="/:user" component={Creator} />
            <Route exact path="/about/security" component={Security} />

            <Route
              exact
              path="/about/community-guidelines"
              component={CommunityGuidelines}
            />
          </div>
          <div className="ui fluid container">
            <Route exact path="/about/how-it-works" component={Workflow} />
          </div>
          <div className="footer-wrap">
            <FooterLinks />
            <FooterCredits />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
