import React, { Component } from "react";

class SingleAkolade extends Component {
  render() {
    return (
      <div className="three wide center aligned column">
        <img
          className="ui circular small image"
          src={this.props.akolade.from.avatar}
          alt={this.props.akolade.from.name}
        />
        <b>{this.props.akolade.from.name}</b>
        <p>
          {this.props.akolade.amount.xlm} {this.props.akolade.amount.interval}{" "}
        </p>
      </div>
    );
  }
}

class Akolades extends Component {
  render() {
    var items = [];
    var akolades = this.props.akolades;
    for (var i = 0; i < akolades.length; i++) {
      items.push(<SingleAkolade akolade={akolades[i]} key={i} />);
    }

    return <div className="ui grid">{items}</div>;
  }
}

class EmptyAkolade extends Component {
  render() {
    return (
      <>
        <div className="ui placeholder">
          <div className="image" />
        </div>
      </>
    );
  }
}

export { Akolades, EmptyAkolade };
