import React, { Component } from "react";
import { Helmet } from "react-helmet";

import { team } from "../../data/team";
import TeamItem from "../TeamItem";

class Team extends Component {
  render() {
    let items = [];
    for (var i = 0; i < team.length; i++) {
      items.push(<TeamItem member={team[i]} key={i} />);
    }
    return (
      <article className="post">
        <Helmet>
          <meta charSet="utf-8" />
          <title>Team</title>
        </Helmet>

        <header className="post-header">
          <h1 className="ui huge header post-title">Team</h1>
        </header>
        <div className="ui text page-text">
          <div className="ui stackable grid">
            <div className="six wide column">
              <p>
                Necessitatibus at corporis doloribus, acium, consectetur vero
                blanditiis minima nostrum numquam, mollitia suscipit aliquid
                illo accusamus praesentium tempore dolore odit hic nobis.
              </p>
            </div>
            <div className="ten wide column">
              <h2>Ecessitatibus at corporis</h2>
              <div className="ui items">{items}</div>
            </div>
          </div>
        </div>
      </article>
    );
  }
}

export default Team;
