import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";

class SingleSmallFeature extends Component {
  render() {
    return (
      <>
        <h3 className="ui header">
          {this.props.singlefeature.title}
          <div className="sub header">
            {this.props.singlefeature.description}
          </div>
        </h3>
      </>
    );
  }
}

const SingleFeature = props => {
  let fitems = [];
  let ifeatures = props.singlefeature.features;
  for (let j = 0; j < ifeatures.length; j++) {
    fitems.push(<SingleSmallFeature singlefeature={ifeatures[j]} key={j} />);
  }
  return (
    <div className="ui grid" data-filter={props.singlefeature.mode}>
      {props.position % 2 === 1 && (
        <div className="eight wide middle aligned column">
          <h2 className="ui big blue header">{props.singlefeature.title}</h2>
          {fitems}
        </div>
      )}
      <div className="eight wide right aligned column">
        <ScrollAnimation
          animateIn="bounceIn"
          animatePreScroll={true}
          offset={100}
          animateOnce={true}
        >
          <img
            className="ui medium right spaced image"
            src={props.singlefeature.image}
            alt={props.singlefeature.title}
          />
        </ScrollAnimation>
      </div>
      {props.position % 2 === 0 && (
        <div className="eight wide middle aligned column">
          <h2 className="ui big blue header">{props.singlefeature.title}</h2>
          {fitems}
        </div>
      )}
    </div>
  );
};

class Feature extends Component {
  render() {
    let items = [];
    let features = this.props.features;
    for (let i = 0; i < features.length; i++) {
      items.push(
        <SingleFeature singlefeature={features[i]} key={i} position={i} />
      );
    }
    return <div className="featurescontainer">{items}</div>;
  }
}

export default Feature;
