import React, { Component } from "react";
import { Helmet } from "react-helmet";
import scrollToElement from "scroll-to-element";

import { howitWorksData, howitWorksPage } from "../../data/howitworks.js";
import Feature from "./Feature";
import Download from "../Download";
let pageTitle = howitWorksPage.title;

class Workflow extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: this.props.mode || "creator",
      howitWorksData: howitWorksData || []
    };
  }

  componentDidMount() {
    this.setMode("creator");
  }

  setMode = mode => {
    let filteredData = howitWorksData.filter(key => {
      return key.mode.indexOf(mode) > -1 ? key : null;
    });
    this.setState({
      howitWorksData: filteredData,
      mode: mode
    });

    scrollToElement(".featurescontainer", {
      offset: 0,
      ease: "inExpo",
      duration: 1000
    });
  };

  render() {
    return (
      <>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{pageTitle}</title>
          <meta name="description" content={howitWorksPage.meta} />
        </Helmet>
        <div className="ui text middle aligned container page-howitworks">
          <div className="ui center aligned grid">
            <div className="ui massive buttons">
              <button
                className={
                  this.state.mode === "creator" ? "ui button blue" : "ui button"
                }
                onClick={() => this.setMode("creator")}
              >
                Creator
              </button>
              <div className="or" />
              <button
                className={
                  this.state.mode === "supporter"
                    ? "ui button blue"
                    : "ui button"
                }
                onClick={() => this.setMode("supporter")}
              >
                Supporter
              </button>
            </div>
          </div>
          <Feature features={this.state.howitWorksData} />
        </div>
        <article className="ui fluid app-download light-blue container">
          <div className="ui text container">
            <Download />
          </div>
        </article>
      </>
    );
  }
}

export default Workflow;
