import React, { Component } from "react";
import { Anchor } from "./Anchor";
import { Display } from "./Display";

import ScrollAnimation from "react-animate-on-scroll";

class SlideShow extends Component {
  constructor(props) {
    super(props);

    this.state = {
      slideIndex: this.props.slideIndex || 0
    };
  }

  changeSlideTo = index => () => {
    this.setState(s => ({ slideIndex: index }));
  };

  render() {
    const { slideIndex } = this.state;
    const slides = this.props.slides;
    return (
      <div className="ui fluid home-app padded container">
        <div className="ui container">
          <h3 className="ui center aligned header">Major Features</h3>
          <div className="ui hidden divider" />
          <ScrollAnimation animateIn="fadeInUp" animateOnce={true}>
            <div className="ui three column middle aligned stackable grid">
              <div className="right aligned column">
                <Anchor
                  {...slides[0]}
                  handleClick={this.changeSlideTo(0)}
                  active={slideIndex === 0 ? "active" : ""}
                />
                <Anchor
                  {...slides[1]}
                  handleClick={this.changeSlideTo(1)}
                  active={slideIndex === 1 ? "active" : ""}
                />
                <Anchor
                  {...slides[2]}
                  handleClick={this.changeSlideTo(2)}
                  active={slideIndex === 2 ? "active" : ""}
                />
              </div>
              <div className="center aligned column">
                <Display
                  imageURL={slides[slideIndex].imageURL}
                  alttag={slides[slideIndex].title}
                />
              </div>
              <div className="left aligned column">
                <Anchor
                  {...slides[3]}
                  handleClick={this.changeSlideTo(3)}
                  active={slideIndex === 3 ? "active" : ""}
                />
                <Anchor
                  {...slides[4]}
                  handleClick={this.changeSlideTo(4)}
                  active={slideIndex === 4 ? "active" : ""}
                />
                <Anchor
                  {...slides[5]}
                  handleClick={this.changeSlideTo(5)}
                  active={slideIndex === 5 ? "active" : ""}
                />
              </div>
            </div>
          </ScrollAnimation>
        </div>
      </div>
    );
  }
}

export default SlideShow;
