import React, { Component } from "react";

class DonationSlabItem extends Component {
  render() {
    return (
      <h4 className="ui small header">
        <div className="content">
          {this.props.value.slot} - {this.props.value.name}
          <div className="sub header">{this.props.value.details}</div>
        </div>
      </h4>
    );
  }
}
class DonationSlabs extends Component {
  render() {
    var items = [];
    var donationSlabs = this.props.donationSlabs;
    for (var i = 0; i < donationSlabs.length; i++) {
      items.push(<DonationSlabItem value={donationSlabs[i]} key={i} />);
    }
    return (
      <>
        <h4 className="ui horizontal divider header">Tiers</h4>
        {items}
      </>
    );
  }
}

class EmptyDonationSlabs extends Component {
  render() {
    return (
      <div className="ui placeholder">
        <div className="image header">
          <div className="line" />
          <div className="line" />
        </div>
      </div>
    );
  }
}

export { DonationSlabs, EmptyDonationSlabs };
