import React, { Component } from "react";

class CtaButton extends Component {
  render() {
    return (
      <>
        <div className="ui hidden divider" />
        <div
          className={
            "ui animated huge button " +
            (this.props.color ? this.props.color : "blue")
          }
        >
          <div className="visible content">
            {this.props.text || "Create your account"}
          </div>
          <div className="hidden content">Finish in 15 seconds</div>
        </div>
      </>
    );
  }
}

export default CtaButton;
