import React, { Component } from "react";

class TeamItem extends Component {
  render() {
    return (
      <div className="item">
        <div className="ui tiny image">
          <img src={this.props.member.imageURL} alt={this.props.member.name} />
        </div>
        <div className="content">
          <h3 className="header">{this.props.member.name}</h3>
          <p className="meta">{this.props.member.positon}</p>
          <div className="description">
            <p dangerouslySetInnerHTML={{ __html: this.props.member.bio }} />
          </div>
        </div>
      </div>
    );
  }
}

export default TeamItem;
